import React from "react"
import { Helmet } from "react-helmet"

import appleTouchIcon from "../../static/apple-touch-icon.png?v=m2RqblwLAj"
import favicon32 from "../../static/favicon-32x32.png?v=m2RqblwLAj"
import favicon16 from "../../static/favicon-16x16.png?v=m2RqblwLAj"
import safariPinnedTab from "../../static/safari-pinned-tab.svg?v=m2RqblwLAj"
import favicon from "../../static/favicon.ico?v=m2RqblwLAj"

const Head = props => {
  return (
    <Helmet>
      <meta charset="utf-8" />

      <title>Calvin Lui</title>

      <link rel="apple-touch-icon" sizes="180x180" href={appleTouchIcon} />
      <link rel="icon" type="image/png" sizes="32x32" href={favicon32} />
      <link rel="icon" type="image/png" sizes="16x16" href={favicon16} />
      <link rel="mask-icon" href={safariPinnedTab} color="#5bbad5" />
      <link rel="shortcut icon" href={favicon} />

      <meta name="msapplication-TileColor" content="#2d89ef" />
      <meta name="theme-color" content="#ffffff" />

      <meta property="og:url" content="https://calvinhklui.com" />
      <meta property="og:image" content="https://i.ibb.co/txkQ0Xk/bitmoji-Thumbs-Up.png" />
      <meta property="og:title" content="Calvin Lui" />
      <meta property="og:description" content="Product Engineer. Interested in Software, Data, &amp; Strategy." />

      <meta name="description" content="Hello there! I'm Calvin. I enjoy working with others to design, build, and ship software products." />
      <meta name="keywords" content="Freelancer, Engineer, Product, Software, Data, JavaScript, Python, Ruby, Swift, Vue.js, React.js, Ruby on Rails, Django, SwiftUI" />
      <meta name="author" content="Calvin Lui" />

      <meta name="ROBOTS" content="NOARCHIVE" />

      <meta name="viewport" content="width=device-width, initial-scale=1.0" />
    </Helmet>
  );
}

export default Head;
