import React, { useContext } from "react"
import "../../styles/bitmoji.scss";

const BitmojiTooltip = props => {
    const { onDarkMode } = useContext(props.context);
    const modeClasses = onDarkMode ? "dark-mode-footer" : "light-mode-footer";
    const tooltipClass = (props.page === "main") ? "bitmoji-tooltip" : "bitmoji-tooltip passkey-tooltip";
  
  return (
    <div className={`${tooltipClass} ${modeClasses}`}>
      Click Me!
    </div>
  );
};

export default BitmojiTooltip;