import React, { useState, useEffect } from "react";
import ReactConfetti from "react-confetti";
import "../../styles/animations.scss";

/**
 * Confetti component that shows a confetti effect
 * @param {boolean} active - Whether the confetti should be shown
 * @param {number} duration - Duration in ms before confetti disappears (0 = never disappear)
 * @param {object} windowSize - Object with width and height properties
 * @returns {React.Component} - Confetti component
 */
const Confetti = ({ active = false, duration = 3000, windowSize, origin = { x: 0.5, y: 0 } }) => {
  const [showConfetti, setShowConfetti] = useState(active);

  useEffect(() => {
    setShowConfetti(active);
    
    // If duration is provided, hide confetti after duration
    if (active && duration > 0) {
      const timer = setTimeout(() => {
        setShowConfetti(false);
      }, duration);
      
      return () => clearTimeout(timer);
    }
  }, [active, duration]);

  if (!showConfetti) return null;

  return (
    <div className="confetti-container">
      <ReactConfetti
        width={windowSize?.width || window.innerWidth}
        height={windowSize?.height || window.innerHeight}
        recycle={false}
        numberOfPieces={300}
        gravity={0.15}
        tweenDuration={6000} 
        friction={0.97}
        colors={[
          '#f44336', '#e91e63', '#9c27b0', '#673ab7', '#3f51b5', 
          '#2196f3', '#03a9f4', '#00bcd4', '#009688', '#4CAF50', 
          '#8BC34A', '#CDDC39', '#FFEB3B', '#FFC107', '#FF9800', '#FF5722'
        ]}
        origin={origin}
        spread={180}
      />
    </div>
  );
};

export default Confetti;
