import { useState, useCallback } from 'react';

/**
 * Custom hook for controlling confetti display
 * @param {number} autoDismissDuration - Duration in ms before confetti disappears (0 = never dismiss automatically)
 * @returns {Object} - Object with showConfetti state and control functions
 */
const useConfetti = (autoDismissDuration = 3000) => {
  const [showConfetti, setShowConfetti] = useState(false);

  const triggerConfetti = useCallback(() => {
    setShowConfetti(true);

    if (autoDismissDuration > 0) {
      setTimeout(() => {
        setShowConfetti(false);
      }, autoDismissDuration);
    }
  }, [autoDismissDuration]);

  const hideConfetti = useCallback(() => {
    setShowConfetti(false);
  }, []);

  return {
    showConfetti,
    triggerConfetti,
    hideConfetti
  };
};

export default useConfetti;
