import React, { useContext, useEffect, useRef } from "react"
import "../../styles/footer.scss"

const Footer = props => {
  const { onDarkMode } = useContext(props.context);
  const modeClasses = onDarkMode ? "dark-mode-footer" : "light-mode-footer";
  const footerRef = useRef(null);
    useEffect(() => {
    const checkScrollPosition = () => {
      if (!footerRef.current) return;
      
      const isScrollable = document.body.scrollHeight > window.innerHeight;
      const scrollPosition = window.scrollY || document.documentElement.scrollTop;
      const windowHeight = window.innerHeight;
      const documentHeight = document.documentElement.scrollHeight;
      const bottomReached = scrollPosition + windowHeight >= documentHeight - 50;
      
      // Get bitmoji container element
      const bitmojiContainer = document.querySelector('.bitmoji-container');
      
      if (isScrollable) {
        if (bottomReached) {
          footerRef.current.classList.add('footer-visible');
          // Ensure bitmoji knows footer is visible
          bitmojiContainer?.classList.add('bitmoji-with-footer');
        } else {
          footerRef.current.classList.remove('footer-visible');
          // Remove the class when footer is not visible
          bitmojiContainer?.classList.remove('bitmoji-with-footer');
        }
        footerRef.current.classList.add('footer-scrollable');
      } else {
        // For non-scrollable pages, always show footer
        footerRef.current.classList.remove('footer-scrollable');
        footerRef.current.classList.add('footer-visible');
        // Make sure bitmoji is positioned correctly on non-scrollable pages
        bitmojiContainer?.classList.add('bitmoji-with-footer');
      }
    };
    
    // Initial check
    checkScrollPosition();
    
    // Set up scroll and resize event listeners
    window.addEventListener('scroll', checkScrollPosition);
    window.addEventListener('resize', checkScrollPosition);
    
    return () => {
      window.removeEventListener('scroll', checkScrollPosition);
      window.removeEventListener('resize', checkScrollPosition);
    };
  }, []);

  return (
    <div className="center-align" id="footer" ref={footerRef}>
      <footer className={`footer-top ${modeClasses}`}>
        <div className="row footer-row">
          <div className="col offset-s1 s10 l12 center-align">
            <br />
            <div className="row hfooter" style={{ fontSize: "20px" }}>
              Calvin Lui &copy; 2025
            </div>
          </div>

          <div className="col offset-s1 s10 l12 center-align footer-last-line">
            <div className="row footer-row">
              Let's build something cool together.
            </div>
            <div className="row">
              Based in 🌤️ San Francisco.
            </div>
          </div>
        </div>
      </footer>
    </div>
  );
}

export default Footer;
